import React, { useEffect } from "react";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import LogoDesignDescription from "../../../components/LogoDesignComponents/LogoDesignDescription";

import SubCategoriesCardLeft from "../../../templates/subcategories/sub-categories-card-left/SubCategoriesCardLeft";

import SubCategoriesCardRight from "../../../templates/subcategories/sub-categories-card-right/SubCategoriesCardRight";

import Pricing from "../../../components/services_page_components/pricing/Pricing";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../../components/genral_components/Footer/Footer";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"
import pic3 from "./pic3.png"
import pic4 from "./pic4.png"

import { useLocation } from "react-router-dom"

import LogoDesignBanner from "./LogoDesignBanner.jpg";

export default function LogoDesign() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="logo-design-main-container">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="Logo Design"
        bannerParagraph="We transform brands into visual masterpieces"
        bannerButton1="LEARN MORE"
        bannerButton2="OUR WORK"
        bannerBackgroundImage={LogoDesignBanner}
      />

      <LogoDesignDescription />

      <SubCategoriesCardLeft
      paragrapgh="We start by being fully immersed in your brand. We have lengthy conversations to fully comprehend your company, target market, values, and objectives. We also research your rivals to find areas where we can differentiate ourselves." 
      picture={pic1}
      />

      <SubCategoriesCardRight paragraph="Using the data gathered, our designers brainstorm and make preliminary sketches of a variety of ideas. we experiment with various strategies and shapes, symbols, and typography to bring your brand's distinctive personality to life."
      picture={pic2} />

      <SubCategoriesCardLeft paragrapgh="We value your feedback at every stage of the process. We'll make the required improvements and adjustments. After the logo design is modern, and the direction has been approved, we painstakingly polish and perfect the selected idea. We make sure that every little thing is excellent, from alignment to proportions to color harmony." 
      picture={pic3}/>

      <SubCategoriesCardRight paragraph="We deliver the finalized logo to you and show you examples of its use in various mockups and scenarios. After receiving your approval, we give you all the required files and formats, giving you the means to successfully incorporate your new logo into all of your branding materials." 
      picture={pic4}/>

      <br/>
      <br/>
      <br/>
      <br/>

      <Pricing/>

      <br/>
      <br/>
      <br/>

      <Skills/>

      <br/>
      <br/>
      <br/>

      <Ourwork/>

      <Footer/>
    </div>
  );
}
